<template>
    <div class="login">
        <!-- <hheader :header="hheader"></hheader> -->
        <div class="container" :style="{backgroundImage:`url(${img})`}">
            <img class="logo" src="../../static/img/logo1.png" alt="">
            <ul class="login-main">
                <li>
                    <i class="iconfont icon-iconzh"></i> <input type="text" v-model="username" placeholder="请输入用户名">
                </li>
                <li>
                    <i class="iconfont icon-mima"></i> <input type="password" v-model="password" placeholder="请输入密码">
                </li>
            </ul>
            <div class="footer">
                <div>
                    <van-checkbox v-model="checked">记住密码</van-checkbox>
                </div>
                <router-link to="/forgetPwd">
                    忘记密码
                </router-link>
            </div>
            <van-button class="hbtn" v-if="!username||!password" disabled>登录</van-button>
            <van-button class="hbtn" v-else type="primary" @click.native="login">登录</van-button>
        </div>
    </div>
</template>

<script>
import { Toast } from "vant";
export default {
    data() {
        return {
            hheader: {
                title: "登录"
            },
            username: "",
            password: "",
            showBtn: 1,
            checked: false,
            img: require("../../static/img/back1.png")
        };
    },
    methods: {
        //登录
        login() {
            let self = this;
             console.log(222222);
            if (self.username == "admin" && self.password == "123456") {
                   console.log(222222);
                let obj = {
                    tokenId: "26465465465465465465",
                    userId: "djfgdfg1d3213d2fg4234"
                };
                self.$store.commit("loginStystem", obj);
                if (self.checked) {
                    localStorage.setItem("pwd", self.password);
                    localStorage.setItem("user", self.username);
                }
                // self.$.Post("/Home/HLogin",{UserName:"test33",PassWord:"123456"},re=>{
                //     console.log(re);
                // })

                //保存账号密码
                self.$router.push("/home");
            } else {
                Toast("账号或密码错误！admin  123456");
            }
        }
    },
    created() {
        let self = this;
        self.password = localStorage.getItem("pwd");
        self.username = localStorage.getItem("user");
        if(self.username&&self.password){
            self.checked=true;
        }
    }
};
</script>


<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.login {
    .container {
        background-image: required(url("../../static/img/back1.png"));
        background-repeat: no-repeat;
        background-size: 100% 100%;
        color: white;
        .logo {
            width: rem(360);
            margin: rem(180) auto 0;
            display: block;
            height: auto;
        }
        .login-main {
            width: 88%;
            margin: rem(120) auto 0;
            li {
                border-bottom: 1px solid white;
                overflow: hidden;
                padding: rem(10) 0;
                margin-bottom: rem(50);
                i {
                    font-size: rem(45);
                    width: 10%;
                    float: left;
                    height: rem(115);
                    line-height: rem(115);
                }
                input {
                    font-size: rem(45);
                    width: 90%;
                    float: left;
                    color: white;
                    height: rem(115);
                    line-height: rem(115);
                    background-color: transparent;
                }
            }
        }
        .footer {
            padding: rem(50) 0;
            font-size: rem(50);
            width: 88%;
            margin: 0 auto;
            color: #ba9906;
            position: relative;
            .van-checkbox--checked {
                background-color: #ba9906;
                border: 0;
            }
            a {
                position: absolute;
                right: 0;
                top: rem(50);
            }
        }
    }
    .hbtn {
        width: 88%;
        display: block;
        margin: rem(20) auto 0;
        color: white;
        letter-spacing: 2px;
        border-radius: rem(70);
        background-color: $color;
        border: 0;
    }
}
</style>



